export const sourceCode = "---\ntitle: Data Visualization with React\ndate: '2025-01-02'\ntags: ['React', 'Data Visualization', 'Charts', 'Frontend', 'Tutorial']\ndescription: Learn how to create beautiful, interactive data visualizations in React with practical examples and best practices.\nenableComment: true\n---\n\n# Data Visualization with React\n\nEffective data visualization transforms complex data into understandable insights. Let's explore how to create interactive visualizations in React.\n\n## Why Visualize Data?\n\n- Makes patterns and trends immediately visible\n- Helps users make data-driven decisions\n- Engages users more than raw numbers\n- Communicates insights effectively\n\n## Building a Simple Bar Chart Component\n\nHere's how to create a reusable bar chart:\n\n```jsx\nfunction BarChart({ data, height = 200 }) {\n  const maxValue = Math.max(...data.map(d => d.value))\n  \n  return (\n    <div style={{ display: 'flex', alignItems: 'flex-end', height }}>\n      {data.map((item) => {\n        const heightPercent = (item.value / maxValue) * 100\n        \n        return (\n          <div key={item.label} style={{ flex: 1, marginRight: 10 }}>\n            <div\n              style={{\n                height: `${heightPercent}%`,\n                backgroundColor: item.color,\n                borderRadius: '4px 4px 0 0',\n                transition: 'height 0.3s ease'\n              }}\n            />\n            <div style={{ textAlign: 'center', marginTop: 5 }}>\n              {item.label}\n            </div>\n          </div>\n        )\n      })}\n    </div>\n  )\n}\n```\n\n## Line Chart with SVG\n\nHere's a simple SVG line chart example:\n\n```jsx\nfunction LineChart({ data }) {\n  const width = 400\n  const height = 200\n  const padding = 20\n  \n  const maxY = Math.max(...data)\n  const minY = Math.min(...data)\n  \n  const getX = (index) => \n    padding + (index / (data.length - 1)) * (width - 2 * padding)\n  \n  const getY = (value) => \n    height - padding - ((value - minY) / (maxY - minY)) * (height - 2 * padding)\n  \n  const pathD = data.map((point, i) => {\n    const x = getX(i)\n    const y = getY(point)\n    return i === 0 ? `M ${x} ${y}` : `L ${x} ${y}`\n  }).join(' ')\n  \n  return (\n    <svg width={width} height={height}>\n      <path\n        d={pathD}\n        fill=\"none\"\n        stroke=\"#10b981\"\n        strokeWidth=\"3\"\n      />\n      {data.map((point, i) => (\n        <circle\n          key={i}\n          cx={getX(i)}\n          cy={getY(point)}\n          r=\"4\"\n          fill=\"#10b981\"\n        />\n      ))}\n    </svg>\n  )\n}\n```\n\n## Popular Chart Libraries\n\n### Recharts\n\nGreat for React-specific charts with declarative syntax:\n\n```jsx\nimport { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip } from 'recharts'\n\nfunction MyChart() {\n  const data = [\n    { name: 'Jan', value: 400 },\n    { name: 'Feb', value: 300 },\n    { name: 'Mar', value: 600 }\n  ]\n\n  return (\n    <LineChart width={500} height={300} data={data}>\n      <CartesianGrid strokeDasharray=\"3 3\" />\n      <XAxis dataKey=\"name\" />\n      <YAxis />\n      <Tooltip />\n      <Line type=\"monotone\" dataKey=\"value\" stroke=\"#8884d8\" />\n    </LineChart>\n  )\n}\n```\n\n### Chart.js with react-chartjs-2\n\nPowerful and flexible:\n\n```jsx\nimport { Line } from 'react-chartjs-2'\n\nfunction MyChart() {\n  const data = {\n    labels: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun'],\n    datasets: [{\n      label: 'Sales',\n      data: [12, 19, 3, 5, 2, 3],\n      borderColor: 'rgb(75, 192, 192)',\n      tension: 0.1\n    }]\n  }\n\n  return <Line data={data} />\n}\n```\n\n### D3.js\n\nMaximum flexibility for custom visualizations:\n\n```jsx\nimport { useEffect, useRef } from 'react'\nimport * as d3 from 'd3'\n\nfunction D3Chart({ data }) {\n  const svgRef = useRef()\n\n  useEffect(() => {\n    const svg = d3.select(svgRef.current)\n    // D3 visualization code here\n  }, [data])\n\n  return <svg ref={svgRef} width={500} height={300} />\n}\n```\n\n## Best Practices\n\n1. **Choose the right chart type** - Bar for comparisons, line for trends, pie for proportions\n2. **Keep it simple** - Don't overwhelm with too much data\n3. **Use color meaningfully** - Consistent colors for categories\n4. **Make it responsive** - Charts should work on all screen sizes\n5. **Add interactivity** - Tooltips, hover effects, and filtering enhance understanding\n6. **Consider accessibility** - Provide alt text and keyboard navigation\n\n## Conclusion\n\nData visualization makes information accessible and actionable. Start with simple charts and gradually add interactivity to create engaging user experiences!";
